"use client"

import React, { useState, useContext } from "react"
import { useFormik } from "formik"
import * as Yup from "yup"
import ThankYouModal from "../Modal/ThankYouModal"
import { useMutation } from "convex/react"
import { createOrder } from "../../../convex/orders"

import { CartContext } from "@/context/CartContext"
import { useRouter } from "next/navigation"

export default function Form() {
  const { cart, clearCart, totalPriceCalc } = useContext(CartContext)
  const createOrderMutation = useMutation(createOrder)
  const router = useRouter()
  const [showModal, setShowModal] = useState(false)
  const [submitting, setSubmitting] = useState(false)

  const phoneRegExp =
    /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/

  const formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      phone: "",
      address: "",
      zip: "",
      city: "",
      country: "",
      payment: "",
    },
    validationSchema: Yup.object({
      name: Yup.string().required("Your name is required"),
      email: Yup.string()
        .email("Wrong format")
        .required("Your email is required"),
      phone: Yup.string()
        .matches(phoneRegExp, "Wrong format")
        .required("Your phone number is required"),
      address: Yup.string().required("Your address is required"),
      zip: Yup.string()
        .max(5, "Enter a valid zip address")
        .required("Zip code is required"),
      city: Yup.string().required("Your city is required"),
      country: Yup.string().required("Your country is required"),
      payment: Yup.string()
        .required("Select a payment method")
        .oneOf(["eMoney", "cod"]),
    }),
    onSubmit: async (values) => {
      if (!cart.length) return alert("Cart is empty")
      setSubmitting(true)

      // Prepare Convex order data (flattened)
      const orderData = {
        customerName: values.name,
        customerEmail: values.email,
        customerPhone: values.phone,
        shippingAddress: values.address,
        city: values.city,
        country: values.country,
        zipCode: values.zip,
        items: cart.map((item) => ({
          productId: item.id,
          name: item.name,
          price: item.price,
          quantity: item.quantity,
          image: item.image,
        })),
        subtotal: totalPriceCalc(),
        shipping: 50, // adjust if dynamic
        tax: 20, // adjust if dynamic
        grandTotal: totalPriceCalc() + 50 + 20,
        paymentMethod: values.payment,
      }

      try {
        // Save order in Convex
        const orderId = await createOrderMutation(orderData)

        // Save to localStorage for order confirmation page
        localStorage.setItem(
          "lastOrder",
          JSON.stringify({ ...orderData, orderId }),
        )

        // Send confirmation email
        await fetch("/api/sendConfirmation", {
          method: "POST",
          body: JSON.stringify({
            orderId,
            customer: {
              name: values.name,
              email: values.email,
            },
            items: cart,
            total: orderData.grandTotal,
          }),
        })

        // Clear cart context and localStorage cart
        clearCart()
        localStorage.removeItem("cart")

        // Show modal
        setShowModal(true)
        document.body.style.overflow = "hidden"
      } catch (err) {
        console.error("Order submission failed:", err)
        alert("Something went wrong. Please try again.")
      } finally {
        setSubmitting(false)
      }
    },
  })

  const modalShow = () => {
    setShowModal(false)
    router.push("/order-confirmation")
  }

  return (
    <form id="checkout-form" onSubmit={formik.handleSubmit}>
      {showModal && (
        <ThankYouModal showModal={showModal} modelState={modalShow} />
      )}

      {/* Billing Details */}
      <div className="pb-[53px]">
        <p className="sub-title text-dark-salmon pb-3">Billing Details</p>
        <div className="flex flex-wrap w-full justify-between gap-y-6">
          {/* Name */}
          <div className="w-[309px]">
            <label className="text-xs font-bold block mb-2">Name</label>
            <input
              className="block rounded-md w-full h-[56px] text-black font-bold text-[14px] focus:outline-none focus:border-dark-salmon border-grey border px-[24px] placeholder:text-black/40"
              placeholder="Your Name"
              name="name"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.name}
            />
            {formik.touched.name && formik.errors.name && (
              <p className="text-red-700 text-xs">{formik.errors.name}</p>
            )}
          </div>

          {/* Email */}
          <div className="w-[309px]">
            <label className="text-xs font-bold block mb-2">Email</label>
            <input
              className="block rounded-md w-full h-[56px] text-black font-bold text-[14px] focus:outline-none focus:border-dark-salmon border-grey border px-[24px] placeholder:text-black/40"
              placeholder="Your Email"
              name="email"
              type="email"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.email}
            />
            {formik.touched.email && formik.errors.email && (
              <p className="text-red-700 text-xs">{formik.errors.email}</p>
            )}
          </div>

          {/* Phone */}
          <div className="w-[309px]">
            <label className="text-xs font-bold block mb-2">Phone</label>
            <input
              className="block rounded-md w-full h-[56px] text-black font-bold text-[14px] focus:outline-none focus:border-dark-salmon border-grey border px-[24px] placeholder:text-black/40"
              placeholder="Your Phone Number"
              name="phone"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.phone}
            />
            {formik.touched.phone && formik.errors.phone && (
              <p className="text-red-700 text-xs">{formik.errors.phone}</p>
            )}
          </div>
        </div>
      </div>

      {/* Shipping Info */}
      <div className="pb-[53px]">
        <p className="sub-title text-dark-salmon pb-3">Shipping Info</p>
        <div className="flex flex-wrap w-full justify-between gap-y-6">
          {/* Address */}
          <div className="w-full">
            <label className="text-xs font-bold block mb-2">Address</label>
            <input
              className="block rounded-md w-full h-[56px] text-black font-bold text-[14px] focus:outline-none focus:border-dark-salmon border-grey border px-[24px] placeholder:text-black/40"
              placeholder="1137 Williams Avenue"
              name="address"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.address}
            />
            {formik.touched.address && formik.errors.address && (
              <p className="text-red-700 text-xs">{formik.errors.address}</p>
            )}
          </div>

          {/* ZIP, City, Country */}
          <div className="w-[309px]">
            <label className="text-xs font-bold block mb-2">ZIP Code</label>
            <input
              className="block rounded-md w-full h-[56px] text-black font-bold text-[14px] focus:outline-none focus:border-dark-salmon border-grey border px-[24px] placeholder:text-black/40"
              placeholder="10001"
              name="zip"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.zip}
            />
            {formik.touched.zip && formik.errors.zip && (
              <p className="text-red-700 text-xs">{formik.errors.zip}</p>
            )}
          </div>
          <div className="w-[309px]">
            <label className="text-xs font-bold block mb-2">City</label>
            <input
              className="block rounded-md w-full h-[56px] text-black font-bold text-[14px] focus:outline-none focus:border-dark-salmon border-grey border px-[24px] placeholder:text-black/40"
              placeholder="New York"
              name="city"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.city}
            />
            {formik.touched.city && formik.errors.city && (
              <p className="text-red-700 text-xs">{formik.errors.city}</p>
            )}
          </div>
          <div className="w-[309px]">
            <label className="text-xs font-bold block mb-2">Country</label>
            <input
              className="block rounded-md w-full h-[56px] text-black font-bold text-[14px] focus:outline-none focus:border-dark-salmon border-grey border px-[24px] placeholder:text-black/40"
              placeholder="United States"
              name="country"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.country}
            />
            {formik.touched.country && formik.errors.country && (
              <p className="text-red-700 text-xs">{formik.errors.country}</p>
            )}
          </div>
        </div>
      </div>

      {/* Payment */}
      <div className="pb-[53px] sm:w-full">
        <p className="sub-title text-dark-salmon pb-3">Payment Details</p>
        <div className="flex w-full h-[128px] mb-5 sm:flex-col">
          <div className="flex-1">
            <p className="text-xs font-bold">Payment Method</p>
            {formik.touched.payment && formik.errors.payment && (
              <p className="text-red-700 text-xs">{formik.errors.payment}</p>
            )}
          </div>
          <div className="flex-1">
            <label className="text-xs font-bold block mb-2 border w-full h-[56px] rounded-md px-4 items-center gap-x-3 hover:border-dark-salmon">
              <input
                name="payment"
                value="eMoney"
                type="radio"
                onChange={formik.handleChange}
              />{" "}
              e-Money
            </label>
            <label className="text-xs font-bold block mb-2 border w-full h-[56px] rounded-md px-4 items-center gap-x-3 hover:border-dark-salmon">
              <input
                name="payment"
                value="cod"
                type="radio"
                onChange={formik.handleChange}
              />{" "}
              Cash on Delivery
            </label>
          </div>
        </div>

        {/* eMoney fields */}
        {formik.values.payment === "eMoney" && (
          <div className="flex flex-wrap sm:pt-8 w-full justify-between gap-y-6">
            <div className="w-[309px]">
              <label className="text-xs font-bold block mb-2">
                e-Money Number
              </label>
              <input
                className="block rounded-md border w-full h-[56px] border-grey text-black font-bold text-[14px] px-[24px]"
                placeholder="238521993"
                type="text"
              />
            </div>
            <div className="w-[309px]">
              <label className="text-xs font-bold block mb-2">
                e-Money PIN
              </label>
              <input
                className="block rounded-md border w-full h-[56px] border-grey text-black font-bold text-[14px] px-[24px]"
                placeholder="6891"
                type="text"
              />
            </div>
          </div>
        )}
      </div>

      <button
        type="submit"
        disabled={submitting}
        className="bg-dark-salmon text-white py-3 px-6 rounded-md font-bold"
      >
        {submitting ? "Processing..." : "Confirm Order"}
      </button>
    </form>
  )
}
